.promotionImage {
  border-radius: 10px;
}

.commandeItem {
  border-bottom: 1px solid rgb(170 170 170 / 28%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0px;
}

.headerPromotion {
  margin-top: 45px;
}

.detailsCommmandeSmall {
  display: flex;
  align-items: center;
}

.promotionContainer {
  height: 55vh;
  overflow: auto;
}

.quantity {
  padding: 10px;
  border: 1px solid #aaa;
  display: block;
  border-radius: 12px;
  margin-left: 15px;
}

.details {
  flex: 1;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(2, 1fr);
}

.PreviewPromotionContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding-bottom: 15px;
  border-bottom: 1px solid #aaaaaa6e;
}

.promotionImage_details {
  width: 200px;
}

.detailsItem div:last-child {
  font-weight: bold;
  font-size: 1.1em;
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 45px;
}

.product div {
  color: #f47106;
  margin-top: 25px;
}

.detailsPromotionOverlay {
  width: 50vw;
}

.actions button {
  margin-left: 15px;
}

.footerDialog {
  display: flex;
  justify-content: flex-end;
}

.commandeImage {
  width: 200px !important;
  margin-right: 50px;
}

.content {
  margin-top: 16px;
}

@media (max-width: 600px) {
  .detailsPromotionOverlay {
    width: 80vw;
  }
  .content {
    flex-direction: column;
    overflow-x: hidden;
  }
  .shitImage {
    width: 70vw !important;
  }
  .createProductScreen {
    padding: 10px !important;
  }
  .headerPromotion {
    margin-top: 0px;
  }

  .PreviewPromotionContainer {
    flex-direction: column;
  }

  .PreviewPromotionContainer .details {
    margin-top: 25px;
  }

  .commandeItem {
    flex-direction: column;
    padding: 18px 0px;
    align-items: flex-start !important;
  }

  .actions {
    display: flex;
    justify-content: space-between;
    margin-top: 11px;
    padding: 0px 0px;
    width: 100%;
  }

  .actions button {
    margin-left: 0px !important;
  }
}
