.material-icons {
  cursor: pointer;
  transition: 0.5s;
}

.p-carousel .p-carousel-indicators .p-carousel-indicator.p-highlight button {
  background-color: #f79a33 !important;
}

.p-rating .p-rating-icon.pi-star {
  color: #f79a33 !important;
}

.p-progressbar .p-progressbar-value {
  background: #f79a33 !important;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 8px;
}

.App {
  height: 100vh;
  width: 100vw;
  display: flex;
}

/* SideBar */

.sideBar {
  width: 250px;
  margin-right: 55px;
  display: flex;
  flex-direction: column;
  transition: 0.5s;
}

.title {
  display: flex;
  align-items: center;
  padding: 12px;

  font-weight: bold;
}

.logoSymbole {
  background-color: #f47106;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 25px;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  color: #fff;
  margin-right: 10px;
}

.linkItem {
  display: flex;
  align-items: center;
  padding: 12px;
  border-top-right-radius: 45px;
  border-bottom-right-radius: 45px;
  cursor: pointer;
}

.linkItem.active {
  background-color: #f4710617;
}

.linkItem.active .labelItem {
  color: #f66a25 !important;
}

.linkItem.active img {
  filter: invert(65%) sepia(84%) saturate(3412%) hue-rotate(348deg)
    brightness(90%) contrast(115%);
}
.linkItem:not(.linkItem.active):hover {
  background-color: #fff;
}

.linkItem img {
  width: 25px;
  margin-right: 15px;
}

.productIcon {
  width: 35px;
}

.navigationLink {
  flex: 1;
}

/* End */

/* Main view of the APP */

.mainApp {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 10px 15px;
  height: 100vh;
  overflow: hidden;
}

/* End */

/* Header */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.searchArea {
  display: flex;
  align-items: center;
  border-radius: 8px;
  background-color: #e3e3e3;
  padding: 15px;
  width: 664px;
  margin-right: 100px;
}

.searchArea:focus-within {
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 1px 3px 1px rgb(60 64 67 / 15%);
}

.searchArea input {
  flex: 1;
  margin-left: 10px;
}

.ThemeContainer {
  width: 55px;
  border-radius: 45px;
  background-color: #dbdbdb;
  display: none;
}

.themeIndicator {
  width: 30px;
  height: 30px;
  /* background-color: rgb(65, 65, 65); */
  background-color: #f47106;
  box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 1px 3px 1px rgb(60 64 67 / 15%);
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.themeIndicator img {
  width: 15px;
}

/* ENd */

/* Route container */

.routerArea {
  flex: 1;

  overflow: auto;
}

.view {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.viewContent {
  flex: 1;
  overflow: auto;
  padding-right: 10px;
}

/* End */

.view {
  animation: fadeIn 1s ease 1;
  overflow: hidden;
}

.toggleNavbar {
  display: none !important;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}

.sideBar.disabled {
  left: -100vw;
}

.sideBar.active {
  left: 0px;
}

.title > div:first-child {
  display: flex;
  align-items: center;
}

.images {
  margin-left: 25px;
}

.overlayDetailsP {
  margin-left: 25px;
}

h1.titleView {
  color: #2e2e2e;
  padding-bottom: 5px;
  /* border-bottom: 1px solid #2e2e2e; */
  margin-bottom: 0px !important;
}

@media (max-width: 600px) {
  .sideBar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5000;
    background-color: #fff;
    height: 100vh;
    width: 80vw;
    box-shadow: 30px 0px 12px #0000003d;
  }

  .searchArea {
    margin-right: 25px !important;
  }

  .ThemeContainer {
    display: none !important;
  }

  .toggleNavbar {
    display: block !important;
  }

  .title {
    align-items: center;
    justify-content: space-between;
  }

  .detailsProducts {
    padding: 15px !important;
  }
}
