.productContainer {
  display: grid;
  grid-gap: 25px;
  /* grid-template-columns: repeat(6, 1fr); */
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 32px;
}

.DetailsProductsBinder {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  z-index: 10;
  /* transition: 0.5s; */
}

.DetailsProductsBinder.inactive {
  transform: translateX(-10px);
  opacity: 0;
  pointer-events: none;
}

.DetailsProductsBinder.active {
  transform: translateX(0px);
  opacity: 1;
  pointer-events: auto;
}

.titleContent h1 {
  margin-bottom: -14px;
}

.productsViewTitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.optionsProductsBut button {
  margin-left: 15px;
}

@media (max-width: 600px) {
  .productsViewTitle {
    flex-direction: column;
  }
  .optionsProductsBut {
    margin-bottom: 15px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .optionsProductsBut button {
    margin-left: 0px !important;
  }
}
