.nameProductPromotion {
  color: #f47106;
}

.durationpromt {
  display: flex;
  justify-content: space-between;
}

.promotionTimeConfirmation {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.validationButtons button {
  margin: 0 12px;
}

.createPromoButton {
  cursor: not-allowed;
  opacity: 0.2;
  pointer-events: none;
}

.createPromoButtonActive {
  cursor: pointer !important;
  opacity: 1;
  pointer-events: auto;
}

.selectDate {
  margin: 20px 0px;
  padding: 12px;
  background-color: #aaaaaa26;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.selectDate input {
  padding: 10px;
  background-color: #ddd;
}
