@font-face {
  font-family: googleFont;
  src: url(./assets/fonts/google_sans.ttf);
}
* {
  box-sizing: border-box;
  transition: 0.5s;
  color: #767676;
  font-family: "googleFont";
}
body {
  margin: 0;
  background-color: #f7f7f7;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  background: transparent;
  border: none;
  outline: none;
}

h1,
h2,
h3,
h4 {
  color: #202124;
}

.toast-demo button {
  min-width: 10rem;
  margin-right: 0.5rem;
}

.p-toast .p-toast-message.p-toast-message-success {
  background: #f5770a !important;
  border: solid #f5770a !important;
  border-width: 0 0 0 6px !important;
  color: #fff !important;
}

.pi-check:before {
  content: "\e909";
  color: #fff;
}

.p-toast .p-toast-message .p-toast-message-content .p-toast-summary {
  font-weight: 700;
  color: #fff;
}

.p-toast .p-toast-message .p-toast-message-content .p-toast-detail {
  margin: 0.5rem 0 0 0;
  color: #fff;
}

.pi-times:before {
  content: "\e90b";
  color: #fff;
}

.p-rating .p-rating-icon {
  color: rgb(70 70 70 / 87%) !important;
}

.MuiButton-containedPrimary .MuiButton-label span {
  color: #fff;
}

.MuiButton-containedPrimary .MuiButton-label {
  color: #fff;
}
