.ventesRevenu {
  display: flex;
  align-items: center;
  justify-content: start;
  margin-bottom: 25px;
}

.ventesRevenu .big {
  padding: 18px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 15px 25px;
}

.ventesRevenu .big div:first-child {
  font-size: 35px;
  font-weight: bold;
}

.ventesRevenu .big div {
  color: #fff !important;
}

.ventesRevenu .ventes {
  background-color: #444;
}

.ventesRevenu .revenu {
  background-color: #f79a33;
}

.evolutionVente {
  margin-top: 25px;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  padding: 0px 35px;
}

.statContainer hr {
  opacity: 0.4;
}

.statContainer {
  overflow: auto;
}

.card {
  padding-right: 200px;
}

.filters .MuiFormControl-root {
  width: 173px;
  margin-left: 15px;
}

.titleStat {
  font-weight: bold;
  font-size: 24px;
}

@media (max-width: 600px) {
  .ventesRevenu,
  .evolutionVente {
    flex-direction: column;
  }
  .ventesRevenu div {
    width: 100%;
  }

  .filters .MuiFormControl-root {
    width: 80vw;
    margin-bottom: 15px;
  }

  .card {
    padding-right: 0 !important;
  }

  .statContainer {
    padding: 13px;
  }
}
