.productItem {
  border-radius: 10px;
  background-color: #fff;
  padding: 12px;
  border: 1px solid #aaaaaa59;
  position: relative;
}

.productItem img {
  width: 90%;
}

.productItem h4 {
  margin: 0;
}

.imageProduct {
  display: flex;
  align-items: center;
  justify-content: center;
}

.namePrice {
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.stock {
  border: 1px solid #f47106;
  font-size: 11px;
  padding: 8px;
  color: #f47106;
  border-radius: 10px;
}

.menuButton {
  position: absolute;
  top: 1px;
  right: 0;
}

.menu {
  position: absolute;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 0px 12px #00000038;
  z-index: 2;
  transition: 0.5s;
  overflow: hidden;

  width: 100%;
  top: 53px;
  right: -5px;
}

.menu.disabled {
  max-height: 0px;
  transform: translateY(-10px);
  opacity: 0.5;
  pointer-events: none;
}

.menu.active {
  max-height: 400px;
  transform: translateY(0px);
  opacity: 1;
  pointer-events: auto;
}

/* Preview screen DOM element binder */
.DetailsProductsBinder {
  overflow: hidden;
}

.DetailsProductsBinder.disabled {
  height: 0px;
  transform: translateY(-10px);
  opacity: 0.5;
  pointer-events: none;
}

.DetailsProductsBinder.active {
  height: 100vh;
  transform: translateY(0px);
  opacity: 1;
  pointer-events: auto;
}

.loaderP {
  display: flex;
}

.deletionOngoing {
  padding: 0 50px;
}
