.cath_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #aaa;
  padding: 10px;
  border-radius: 8px;
}

.viewContent.cath {
  padding-right: 0 !important;
  margin-top: 15px;
}

.create_cat {
  display: flex;
  align-items: center;
}

.create_cat input {
  padding: 12px;
  border: 1px solid #aaa;
  flex: 1;
  margin-left: 25px;
  border-radius: 8px;
}

@media (max-width: 600px) {
  .viewContent.cath .productContainer {
    padding: 12px;
  }

  .create_cat {
    flex-direction: column;
    align-items: flex-start !important;
  }
  .create_cat input {
    margin-left: 0px;
    width: 100%;
  }
}
