.detailsProducts {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  overflow-y: auto;
  width: 100vw;
  z-index: 3;
  padding: 25px 10vw;
  background-color: #fff;
}
.headerDetailsProducts {
  display: flex;
  align-items: center;
}

.detailsProducts img {
  border-radius: 10px;
}

.headerDetailsProducts .titleDetailsProducts {
  margin-left: 25px;
}

.shitImage {
  width: 400px !important;
}

.extraImages {
  margin-top: 25px;
  width: 403px;
}

.extraImages img {
  width: 100px;
}

.content {
  display: flex;
}

.detailsP {
  flex: 1;
}

.ratingsReviewsSells {
  pointer-events: none;
  font-size: 18px;
  display: flex;
  align-items: center;
}

.ratingsReviewsSells div {
  margin-right: 20px;
}

.orders {
  color: #f79300;
  display: flex;
}

.orders span {
  color: #f79a33;
}

.reviews {
  color: #aaa;
}

.description {
  color: #aaa;
}

.features {
  margin-top: 33px;
}

.features .featureItem:nth-child(odd) {
  background-color: #f7f7f7;
}

.featureItem {
  display: flex;
  padding: 12px 0px;
}

.featureItem .titleF {
  width: 100px;
  color: #aaa;
}

.featureItem .valueF {
  flex: 1;
  color: rgb(34, 34, 34);
  font-weight: bold;
}

.loader.disabled {
  /* display: none; */

  transform: translateY(-10px);
  opacity: 0;
  pointer-events: none;
}
.loader.active {
  /* display: block; */
  transform: translateY(0px);
  opacity: 1;
  /* pointer-events: auto; */
}

@media (max-width: 600px) {
  .ratingsReviewsSells {
    flex-direction: column;
    align-items: start;
  }
  .ratingsReviewsSells div {
    margin: 5px 0px;
  }
}
