.dividerSettings {
  opacity: 0.3;
}

.titleMessageSettings {
  width: 50vw;
}

.accountSettingsWrapper {
  margin-top: 30px;
  overflow: auto;
  height: 60vh;
}

.settingsItem {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.inputArea {
  padding: 12px;
  border: 1px solid #aaa;
  border-radius: 8px;
  width: 35vw;
  margin-left: 10vw;
}

.settingsItem .label {
  width: 200px;
}
.settingsItem.logo .inputArea {
  border: none !important;
}

.settingsItem .inputArea img {
  width: 100px;
  clip-path: circle();
  cursor: pointer;
}

.accountSettingsWrapper .footerUI {
  margin-right: 45px;
}

.accountSettingsWrapper select,
.accountSettingsWrapper input {
  width: 100%;
  outline: none;
  border: none;
  background-color: transparent;
}

.accountSettingsWrapper select:focus {
  outline: none;
  border: none;
}

.settingsView h1 {
  margin-bottom: 0px !important;
}

@media (max-width: 600px) {
  .titleMessageSettings {
    width: 100%;
  }
  .accountSettingsWrapper {
    padding: 0px 10px;
  }
  .inputArea {
    width: 100%;
    margin-left: 0 !important;
  }
}
