.createProductScreen {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  overflow-y: auto;
  width: 100vw;
  z-index: 3;
  padding: 1px 10vw;
  background-color: #fff;
}
.createProductScreen img {
  border-radius: 10px;
}

.createProductScreen.disabled {
  /* display: none; */

  transform: translateY(-10px);
  opacity: 0;
  pointer-events: none;
}
.createProductScreen.active {
  /* display: block; */
  transform: translateY(0px);
  opacity: 1;
  pointer-events: auto;
}

.loaderCreateProduct.disabled {
  /* display: none; */

  transform: translateY(-10px);
  opacity: 0;
  pointer-events: none;
}

.loaderCreateProduct.active {
  /* display: none; */

  transform: translateY(0px);
  opacity: 1;
  pointer-events: auto;
}

.headerDetailsProducts.createProduct {
  justify-content: flex-end;
}

.featuresInput {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 35px;
}

.footerUI {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  margin-top: 65px;
  padding-top: 30px;
  border-top: 1px solid #aaaaaa52;
}

.footerUI button {
  margin-left: 15px;
}

.loadImageTrigger {
  cursor: pointer;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px dashed #aaaaaa94;
  padding: 28px;
  border-radius: 10px;
}

.loadImageTrigger img {
  width: 50%;
  opacity: 0.2;
}

.totalImages {
  margin: 25px 0px;
  padding: 10px 0px;
  border-top: 1px solid rgba(170, 170, 170, 0.603);
  display: flex;
  align-items: center;
  justify-content: center;
}

.descriptionProduct {
  grid-column: 3/1;
}

.product-item {
  position: relative;
}

.Delete-Image {
  position: absolute;
  top: -9px;
  left: 0;
  z-index: 2000;
}

.Delete-Image .MuiIconButton-root {
  background: white;
  box-shadow: 0px 0px 12px #aaa;
  transform: scale(0.5);
}

.loadImageTrigger {
  width: 400px;
}

.footerCreateProduct {
  padding-top: 20px;
  border-top: 1px solid rgba(170, 170, 170, 0.514);
  display: flex;
  justify-content: flex-end;
  margin-top: 22px;
}
.footerCreateProduct button {
  margin-left: 15px;
}
@media (max-width: 600px) {
  .detailsP {
    margin-left: 0px !important;
  }
  .loadImageTrigger {
    width: 100% !important;
  }
}
